
.register-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #f9fafb;
}

.register-box {
  background: white;
  padding: 30px 40px;
  border-radius: 10px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  width: 320px;
  text-align: center;
}

.register-box h2 {
  margin-bottom: 20px;
  color: #333;
}

.register-box input {
  width: 100%;
  padding: 10px 12px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

.register-box button {
  width: 100%;
  padding: 10px;
  background-color: #10b981;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-top: 10px;
}

.register-box button:hover {
  background-color: #059669;
}

.register-box p {
  margin-top: 15px;
  font-size: 14px;
}
